import { Form } from 'react-bootstrap'
import { Button } from 'react-bootstrap';
import { Container, Row, Col, Card, InputGroup, FormControl } from 'react-bootstrap'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faUserCircle, faKey } from '@fortawesome/free-solid-svg-icons'
import Image from 'react-bootstrap/Image'
import logo from '../images/logo.png'
import facebook from '../images/icons8-facebook.svg'
import instagram from '../images/icons8-instagram.svg'
import twitter from '../images/icons8-twitter.svg'
import './Auth.css'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'

const MySwal = withReactContent(Swal)


var loginEvent = async function () {
  let myHeaders = new Headers();
  myHeaders.append('Content-Type', 'application/json');
  const requestDomain = document.getElementById("requestPath").getAttribute("data-server");
  let resp = await fetch(requestDomain + "/login",
    {
      method: "POST",
      headers: myHeaders,
      body: JSON.stringify(
        {
          user: document.getElementById("user").value,
          pass: document.getElementById("password").value
        }
      )
    }
  );
  let auxResp = await resp.json();

  await MySwal.fire({
    title: "<strong>" + auxResp.mssg + "</strong>",
    html: (auxResp.status === 1) ? <i>Bienvenido!</i> : <i>Verifíca tus datos!</i>,
    icon: (auxResp.status === 1) ? 'success' : "error"
  })

  if (auxResp.status === 1)
    window.location = "/admin"

  return;
};

var cardComponent = function () {
  return (
    <Row>
      <Col md={{ span: 6, offset: 3 }} className="card-align">
        <Card className="card-background text-center">
          <Card.Body>
            <Card.Title >
              <h1><span className="Rectangle-1">B</span>ienvenido a Shop Intelligence</h1>
            </Card.Title>
            <Card.Subtitle className="mb-2 text-light">By Ares Shop</Card.Subtitle>
            <Card.Text className="mt-5">
              La única tienda que en verdad conoce a sus clientes y que les genera el valor que se merecen.
            </Card.Text>
          </Card.Body>
        </Card>
      </Col>
      <Col className="text-center" style={{ marginTop: "10vh" }} sm={12} md={{ span: 6, offset: 3 }}>
        <Image src={facebook} style={{ width: "5rem" }} />
        <Image src={instagram} style={{ width: "5rem" }} />
        <Image src={twitter} style={{ width: "5rem" }} />
      </Col>
    </Row>
  );
}

var loginComponent = function () {
  return (
    <Row>
      <Col xs={{ span: 6, offset: 6 }}>
        <Button className="button-registrarse">
          Registrarse
        </Button>
      </Col>
      <Col xs={12} className="login-div text-center" >
        <Image src={logo} style={{ width: "10rem" }} />
        <br />
        <h6>Accede a tu cuenta</h6>
        <br />
        <br />
        <>
          <Form.Label htmlFor="basic-url" style={{ float: "left" }}> <strong>Nombre de usuario</strong></Form.Label>
          <InputGroup className="mb-3">
            <InputGroup.Text id="basic-addon1" className="font-radius">
              <FontAwesomeIcon icon={faUserCircle} />
            </InputGroup.Text>
            <FormControl
              placeholder="ejemplo@ejemplo.com"
              aria-label="Email"
              aria-describedby="email"
              className="input-login"
              id="user"
            />
          </InputGroup>
        </>
        <>
          <Form.Label htmlFor="basic-url" style={{ float: "left" }}> <strong>Contraseña</strong></Form.Label>
          <InputGroup className="mb-3">
            <InputGroup.Text id="basic-addon1" className="font-radius">
              <FontAwesomeIcon icon={faKey} />
            </InputGroup.Text>
            <FormControl
              placeholder="********"
              aria-label="Password"
              aria-describedby="password"
              className="input-login"
              type="password"
              id="password"
            />
          </InputGroup>
        </>
        <>
          <br />
          <Button variant="link" href="/resetPassword">¿Olvidaste tu contraseña?</Button>
          <br />
          <Button className="btn-warning btn-login" onClick={loginEvent}>Iniciar sesión</Button>
          <br />
          <br />
          <Button variant="link" className="text-dark">¿Necesitas ayuda?</Button>
        </>
      </Col>
    </Row>
  );
}

//const modalText = [{title: "Inicio de sesión", image: "CheckValid", modalP: "¡Exitoso!"}];
function Auth(props) {
  return (
    <Container fluid>
      <Row>
        <Col xs={12} md={8} lg={9} className="fondo-welcome">
          {cardComponent()}
        </Col>
        <Col xs={12} md={4} lg={3} style={{ backgroundColor: "#ffd700", padding: "1.5rem" }}>
          {loginComponent()}
        </Col>
      </Row>
    </Container>
  )

}

export default Auth
